import { getImagesByQuery } from './js/pixabay-api.js';
import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions.js';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const searchForm = document.querySelector('.form');
const loadMoreButton = document.getElementById('load-more');
let currentQuery = '';
let currentPage = 1;

searchForm.addEventListener('submit', async event => {
  event.preventDefault();
  currentQuery = event.target.elements['search-text'].value.trim();
  currentPage = 1;

  if (!currentQuery) {
    return iziToast.error({ message: '–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å –∑–∞–ø–∏—Ç!' });
  }

  clearGallery();
  hideLoadMoreButton();
  showLoader();

  try {
    const response = await getImagesByQuery(currentQuery, currentPage);
    hideLoader();

    if (!response || !response.hits || response.hits.length === 0) {
      iziToast.warning({
        message:
          'Sorry, there are no images matching your search query. Please try again!',
      });
    } else {
      createGallery(response.hits);
      showLoadMoreButton();
    }
  } catch (error) {
    handleError(error);
  }
});

loadMoreButton.addEventListener('click', async () => {
  currentPage++;
  showLoader();

  try {
    const response = await getImagesByQuery(currentQuery, currentPage);
    hideLoader();

    if (!response || !response.hits || response.hits.length === 0) {
      hideLoadMoreButton();
      iziToast.info({ message: "You've reached the end of search results." });
    } else {
      createGallery(response.hits);
    }
  } catch (error) {
    handleError(error);
  }
});

function handleError(error) {
  console.error('–ü–æ–º–∏–ª–∫–∞ –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å:', error.message || error);
  iziToast.error({ message: '–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è!' });
}
